.ng2-c-pull-to-refresh {
  display: block;
  min-height: 100%;
  position: relative; }

.ng2-c-pull-to-refresh--animate .ng2-c-pull-to-refresh__content {
  transition: transform 0.3s ease-out; }

.ng2-c-pull-to-refresh--loading .ng2-c-pull-to-refresh__content::before {
  animation: spinner 600ms infinite;
  content: '\e935'; }

.ng2-c-pull-to-refresh--pull .ng2-c-pull-to-refresh__content::before,
.ng2-c-pull-to-refresh--release .ng2-c-pull-to-refresh__content::before {
  content: '\e933'; }

.ng2-c-pull-to-refresh--release .ng2-c-pull-to-refresh__content::before {
  transform: rotate(180deg); }

.ng2-c-pull-to-refresh__content {
  position: relative; }
  .ng2-c-pull-to-refresh__content::before {
    color: #cccccc;
    content: '';
    display: inline-block;
    font-family: "delen";
    font-size: 30px;
    height: 60px;
    left: 0;
    line-height: 60px;
    overflow: hidden;
    position: absolute;
    text-align: center;
    top: -60px;
    transition: transform 0.3s ease-out;
    width: 100%; }

@keyframes spinner {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
